body {
  background: #fff1fe;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem;
}

h1 {
  text-align: center;
  color: #333;
}

.form {
  background: #fff;
  border-radius: 8px;
  margin-bottom: 2rem;
  padding: 1rem;
  box-shadow: 0 0 10px #0000001a;
}

.form-title, .form-text {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  margin-bottom: 1rem;
  padding: .5rem;
  font-size: 1rem;
  display: block;
}

.form button {
  color: #fff;
  cursor: pointer;
  background-color: #518182;
  border: none;
  border-radius: 5px;
  padding: .6rem 1.2rem;
  font-size: 1rem;
}

.posts {
  padding: 0;
  list-style: none;
}

.posts li {
  background: #fff;
  border-radius: 5px;
  margin-bottom: 1rem;
  padding: 1rem;
  box-shadow: 0 0 5px #0000001a;
}

.posts h3 {
  margin-top: 0;
}

.posts p {
  margin: .5rem 0;
}

.posts button {
  color: #333;
  cursor: pointer;
  background-color: #ffd1d1;
  border: none;
  border-radius: 4px;
  margin-right: .5rem;
  padding: .4rem .8rem;
}

.comments-section {
  background: #fff;
  border-radius: 5px;
  margin-top: 3rem;
  padding: 1rem;
}

.external-comments {
  margin: 1rem 0;
  padding: 0;
  list-style: none;
}

.comment-item {
  border-bottom: 1px solid #ccc;
  padding: 1rem 0;
}

.comment-item h4 {
  color: #333;
  margin: 0 0 .5rem;
  font-size: 1.1rem;
}

.comment-item p {
  margin: .2rem 0;
}

.load-more-btn {
  color: #fff;
  cursor: pointer;
  background-color: #f88;
  border: none;
  border-radius: 5px;
  padding: .5rem 1rem;
  transition: background .3s;
}

.load-more-btn:hover {
  background-color: #f55;
}
